export default class Accesorio{
    constructor (
        id=null,
        tipo_accesorio=null,
        estado=null,
        equipo_id=null,
    ) {
        this.id = id;
        this.tipo_accesorio = tipo_accesorio;
        this.estado = estado;
        this.equipo_id = equipo_id;
    }
}


